.accordion {
    position: relative;
}

[id*="open-accordion"],
[id*="close-accordion"] {
    display: block;
    font-size: 1.25em;
    font-weight: normal;
    line-height: 40px;
    padding-left: 2%;
    position: relative;
    text-decoration: none;
    text-shadow: none;
    width: 100%;
}

[id*="close-accordion"] {
    display: none;
}

.target-fix {
    display: block;
    top: 0;
    left: 0;
    position: fixed;
}

.accordion-content {
    height: 0;
    margin: -1px auto 0;
    padding: 0 2.5%;
    position: relative;
    overflow: hidden;
    width: 90%;
    transition: all 0.1s ease;
}

.accordion span:target ~ .accordion-content {
    display: block;
    height: auto;
    padding-bottom: 25px;
    padding-top: 10px;
}

.accordion span:target ~ [id*="close-accordion"] {
    display: block;
}

.accordion span:target ~ [id*="open-accordion"] {
    display: none;
}
